import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { VhPurchaseInvoice } from 'src/app/sales/interface/vh-purchase-invoice';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-purchase',
  templateUrl: './purchase.component.html',
  styleUrls: ['./purchase.component.scss']
})
export class PurchaseComponent implements OnInit {

  public searchValue: string = ''
  public invoiceShow: VhPurchaseInvoice[] = []
  public invoiceToday: VhPurchaseInvoice[];
  dataRestore: any = this.router.getCurrentNavigation().extras.state
  startTime: Date = new Date()
  endTime: Date = new Date()
  listFilter: Array<any> = [{ value: 1, name: "Latest" }, { value: 2, name: "Oldest" }, { value: 3, name: "Low to High value" }, { value: 4, name: "High to Low value" }]
  tableHeight: string;
  constructor(
    private vhQuery: VhQuery,
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public vhAlgorithm: VhAlgorithm,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef
  ) {
    this.startTime.setHours(0, 0, 0, 0)
    this.endTime.setHours(23, 59, 59, 59)
  }

  ngOnInit() {
    if (this.dataRestore) {
      this.searchValue = this.dataRestore.searchValue
      this.filterType = this.dataRestore.filterType
       
    }
    else { this.filterType = 1;  }
    if (!this.invoiceToday) this.vhComponent.showLoading("", "transparent-loading", null, 0, false).then(() => this.getInvoiceToday(this.filterType))
    else this.getInvoiceToday(this.filterType)
  }
  
  /**
   * lọc bill theo ['bill_code', 'partner_name', 'payment_name', 'status', 'employee_name']
   * @example this.searchBill('')
   */
  searchBill(value: string) {
    if (value && value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.invoiceShow = this.vhAlgorithm.searchList(val, this.invoiceToday, ['bill_code', 'partner_name', 'payment_name', 'status'])
    } else this.invoiceShow = [...this.invoiceToday]
  }

  /**
   * get danh sách hóa đơn nhập hàng từ DB
   * @example this.getInvoiceToday(1)
   */
  getInvoiceToday(filterType?) {
    if (this.vhAuth.checkMyPermission("purchase_enable_view_bill_today"))
      this.vhQuerySales.getBills_byFields( { date: { $gte: this.startTime, $lte: this.endTime }, bill_type: { $in: [2, 6, 22] }, id_branch: { $eq: this.vhQuerySales.getDefaultBranch()._id } }, {})
        .then((invoice: VhPurchaseInvoice[]) => {
          if (!this.invoiceToday) this.vhComponent.hideLoading(0)

          this.invoiceToday = invoice.map(item => {
            return { ...item, employee_name: this.vhAuth.getlocalEmployee(item.id_employee).name, partner_name: this.renderPartner(item.id_supplier), payment_name: this.renderPayment(item.payment_type, item.id_wallet), status: this.renderStatus(item.bill_type) }
          })
          
          if (filterType) this.sortFilter(filterType)
          else this.searchBill(this.searchValue)
        })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  /**
   * trả về tên loại trang thái bill
   * @param bill_type 
   * @returns string
   * @example let status_name = this.renderStatus(2)
   */
  renderStatus(bill_type: 2 | 6 | 22) {
    switch (bill_type) {
      case 6: return this.languageService.translate('Temporary')
      case 22: return this.languageService.translate('Cancelled')
      default: return this.languageService.translate('Entered')
    }
  }
  /**
   * trả về tên ncc của bill
   * @param id_partner 
   * @returns string
   * @example let supplier_name = this.renderPartner('DNIKWUJHNBDUYWGU') 
   */
  renderPartner(id_partner: string) {
    let supplier: string = this.vhQuerySales.getlocalSupplier(id_partner).name

    return supplier ? supplier : this.languageService.translate("Not found")
  }
  
  /**
   * trả về tên phương thức thanh toán của đơn
   * @example let payment_name= this.renderPayment(1)
   */
  renderPayment(payment_type: 1 | 2 | 3, id_wallet: string = null) {
    switch (payment_type) {
      case 1: return this.languageService.translate('Cash')
      case 2: return this.languageService.translate('Debit')
      case 3: return this.vhQuerySales.getlocalWallet(id_wallet).name
    }
  }

  /**
   * router qua trang tạo bill
   * @example this.goToCart()
   */
  goToCart() {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType }
    if (this.vhAuth.checkMyPermission("purchase_enable_create_bill")) this.router.navigate(["/sales/dashboard/manage/invoice/purchase/add-purchase"], {
      state: { dataRestore }
    })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  /**
   * router qua trang chi tiết bill
   * @example this.goToDetail('DMNWIDHNBIUWHGDUWY')
   */
  goToDetail(id) {
    let dataRestore = { searchValue: this.searchValue, filterType: this.filterType,  invoice: this.invoiceShow}
    if (this.vhAuth.checkMyPermission("purchase_enable_view_bill_today")) this.router.navigate(["/sales/dashboard/manage/invoice/purchase/detail"], { state: { id: id, dataRestore } })
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  public filterType: 1 | 2 | 3 | 4 = 2;
  
  /**
   * lọc hóa đơn theo date hoặc total
   * @example this.sortFilter(1)
   */
  sortFilter(filterType: 1 | 2 | 3 | 4) {
    this.filterType = filterType
    switch (filterType) {
      case 1: this.invoiceToday.sort((prev, next) => Date.parse(next.date) - Date.parse(prev.date));  break;
      case 2: this.invoiceToday.sort((prev, next) => Date.parse(prev.date) - Date.parse(next.date));  break;
      case 3: this.invoiceToday.sort((prev, next) => (prev.total * (1 + prev.tax / 100)) - (next.total * (1 + next.tax / 100)));  break;
      case 4: this.invoiceToday.sort((prev, next) => (next.total * (1 + next.tax / 100)) - (prev.total * (1 + prev.tax / 100)));  break;
    }

    this.searchBill(this.searchValue)
  }
  onCurrentPageDataChange(event) { }
  ngAfterViewChecked() {
    if (document.querySelector("#purchase-invoice-today") && document.querySelector(".ant-table-thead") && document.querySelector(".purchase-invoice-today-header") && document.querySelector(".ant-table-pagination")) {
      this.tableHeight = document.querySelector("#purchase-invoice-today").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".purchase-invoice-today-header").clientHeight - document.querySelector(".ant-table-pagination").clientHeight - 80 + "px";
    }
    this.cdRef.detectChanges();
  }
  goBack() {
    this.router.navigate(["/sales/dashboard/manage/invoice"])
  }
}
