import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PurchaseComponent } from './purchase.component';
import { DetailComponent } from './detail/detail.component';
import { AddPurchaseComponent } from './add-purchase/add-purchase.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { PrintLotModule } from 'src/app/sales/components/print-lot/print-lot.module';
import { BillType2Module } from 'src/app/sales/components/bill-type/bill-type-2/bill-type2.module';
import { EditComponent } from './edit/edit.component';
const routes: Routes = [
  {
    path: "",
    children: [
      {
        path: "",
        component: PurchaseComponent
      },
      {
        path: "detail",
        component: DetailComponent
      },
      {
        path: "add-purchase",
        component: AddPurchaseComponent,
      
      },
      {
        path: "edit-purchase",
        component: EditComponent,
       
      }
    ]
  }
]

@NgModule({
  declarations: [PurchaseComponent, DetailComponent, AddPurchaseComponent, EditComponent,  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    ReactiveFormsModule,
    FormsModule,
    TranslateModule,
    ScrollingModule,
    BillType2Module,
    PrintLotModule
  ], 
})
export class PurchaseModule { }
